//fv.scss
@use "../../setting/setting" as f;

.greeting {
	position: relative;
}

.greeting__goldBox {
	position: absolute;
	right: 0;
	top: 0;
	max-width: 410px;

	@media screen and (max-width: 600px) {
		max-width: 200px;
	}
}

.greeting__inner {
	padding: 140px 20px 72px;

	@media screen and (max-width: 1000px) {
		padding: 140px 0 72px;
	}

	@media screen and (max-width: 600px) {
		padding: 40px 0 72px;
	}
}

.greeting__title {
	text-align: center;
	color: f.$txt-color;
}

.greeting__textTop {
	max-width: 900px;
	margin-top: 72px;
	@media screen and (max-width: 1000px) {
		padding: 0 20px;
		margin-top: 40px;
	}
}

.greeting__textBottom {
	@media screen and (max-width: 1000px) {
		padding: 0 20px;
	}
}

.greeting__textMiddle {
	@media screen and (max-width: 1000px) {
		padding: 0 20px;
	}
}

.greeting__mediaBox {
	display: flex;
	column-gap: 80px;
	margin-top: 54px;
	margin-bottom: 80px;

	@media screen and (max-width: 1000px) {
		flex-direction: column;
		align-items: flex-end;
	}

	img {
		max-width: 500px;
		width: 100%;
		padding-left: 20px;
		margin-top: 54px;
	}
}
