//product.scss
@use "../../setting/setting" as f;

.product {
	background: url(../img/home/about-back-pc.jpg) center center / cover no-repeat;
	position: relative;
	overflow-x: hidden;
}

.product__inner {
	padding: 125px 0 74px;

	@media screen and (max-width: 1000px) {
		padding: 90px 0 74px;
	}
}

.product__title {
	text-align: center;
	color: f.$txt-color;

	@media screen and (max-width: 600px) {
		font-size: 2.4rem;
	}
}

.product__contentsTop {
	display: flex;
	align-items: center;
	gap: 100px;
	margin-top: 96px;

	@media screen and (max-width: 1200px) {
		gap: 32px;
	}

	@media screen and (max-width: 1000px) {
		flex-direction: column;
		margin-top: 60px;
		gap: unset;
	}
}

.product__contentsTopImg {
	flex: 1;
	max-width: 52%;

	@media screen and (max-width: 1000px) {
		max-width: 80%;
	}

	@media screen and (max-width: 600px) {
		width: 100%;
		object-fit: contain;
		max-width: unset;
	}
}

.product__textBox {
	width: 570px;
	padding-right: 32px;

	@media screen and (max-width: 1200px) {
		padding: 0 20px;
		display: flex;
		flex-direction: column;
		max-width: 570px;
		width: auto;
	}
}

.product__textTitle {
	font-size: 3.5rem;
	margin-top: 48px;

	@media screen and (max-width: 600px) {
		font-size: 2.2rem;
	}
}

.product__text {
	margin-top: 36px;
	letter-spacing: 0.12em;
}

.product__contentsBtn {
	margin-left: 180px;
	margin-top: 130px;

	@media screen and (max-width: 1530px) {
		margin-left: 140px;
		align-self: center;
	}

	@media screen and (max-width: 1440px) {
		margin-left: 55px;
		margin-top: 50px;
	}

	@media screen and (max-width: 1000px) {
		align-self: center;
		margin: 50px auto 0 !important;
		text-align: center;
	}

	@media screen and (max-width: 600px) {
		width: 80%;
	}
}

.product__slide {
	margin-top: 60px;
	cursor: pointer;
}

.swiper-slide {
	// width: 455px !important;
	max-width: 455px;
	height: 100%;

	img {
		max-width: 100%;
		height: auto;
	}
}
