//fv.scss
@use "../../setting/setting" as f;

.fv {
	background: url(../img/home/main-pc.jpg) center center / cover no-repeat fixed;
	width: 100vw;
	height: 814px;
	background-size: cover;
	position: relative;
	color: f.$white-color;

	@media screen and (max-width: 600px) {
		background: url(../img/home/main-sp.png) center center / cover no-repeat;
		height: 650px;
	}
}

.fv__header {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	gap: 35px;
	margin-right: 40px;
	@media screen and (max-width: 600px) {
		margin-right: 20px;
	}

	ul {
		@media screen and (max-width: 1000px) {
			display: none;
		}
	}

	a {
		@media screen and (max-width: 1000px) {
			display: none;
		}
	}
}

.fv__nav {
	font-size: 16px;
	padding-top: 35px;

	ul {
		display: flex;
		gap: 35px;
	}
}

.fv__navItem {
	a {
		color: f.$white-color;
		font-size: 20px;
		&:hover .fv__navItemText::after {
			width: 100%; /*hover時に表示*/
		}
		.fv__navItemText {
			position: relative;
			z-index: 1;
			&::after {
				content: "";
				position: absolute;
				left: 0;
				bottom: -10px; /*テキストからの距離*/
				width: 0%; /*初期状態では下線非表示*/
				height: 2px; /*下線の高さ*/
				background: #fff; /*下線の色*/
				z-index: -1;
				transition: all 0.4s; /*アニメーション速度*/
			}
		}
	}
}

.fv__instaIcon {
	width: 30px;
}

.fv__logoBox {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;

	@media screen and (max-width: 600px) {
		max-width: 380px;
		width: 100%;
	}
}

.fv__logo {
	width: 225px;

	@media screen and (max-width: 600px) {
		width: 184px;
	}
}

.fv__h1 {
	margin-top: 70px;

	@media screen and (max-width: 600px) {
		font-size: 3.4rem;
	}

	@media screen and (max-width: 380px) {
		font-size: 3rem;
	}
}

.fv__scroll {
	transform: rotate(90deg);
	position: absolute;
	bottom: 180px;
	right: 5%;

	@media screen and (max-width: 600px) {
		bottom: 120px;
		display: none;
	}

	div {
		font-family: f.$english;
		color: f.$white-color;
		display: block;
		position: relative;
		font-size: 3rem;

		@media screen and (max-width: 600px) {
			font-size: 1.4rem;
		}

		&::after {
			position: absolute;
			top: 50%;
			right: -100px;
			background-color: f.$white-color;
			content: "";
			width: 110px;
			height: 1px;
			content: "";
			transform: rotate(180deg);
			/*線の動き1.4秒かけて動く。永遠にループ*/
			animation: pathmove 1.8s ease-in-out infinite;
			opacity: 0;

			@media screen and (max-width: 600px) {
				width: 90px;
			}
		}

		/*高さ・位置・透過が変化して線が上から下に動く*/
		@keyframes pathmove {
			0% {
				width: 0;
				right: -20px;
				opacity: 0;
			}

			70% {
				width: 60px;
				opacity: 1;
			}

			100% {
				width: 0;
				right: -150px;
				opacity: 0;
			}
		}
	}
}
.fv__scrollSp {
	display: none;
	transform: rotate(90deg);
	position: absolute;
	bottom: 180px;
	right: 1%;

	@media screen and (max-width: 600px) {
		bottom: 120px;
		display: block;
	}

	div {
		font-family: f.$english;
		color: f.$white-color;
		display: block;
		position: relative;
		font-size: 3rem;

		@media screen and (max-width: 600px) {
			font-size: 1.4rem;
		}

		&::after {
			position: absolute;
			top: 50%;
			right: -100px;
			background-color: f.$white-color;
			content: "";
			width: 110px;
			height: 1px;
			content: "";
			transform: rotate(180deg);
			/*線の動き1.4秒かけて動く。永遠にループ*/
			animation: pathmove 1.8s ease-in-out infinite;
			opacity: 0;

			@media screen and (max-width: 600px) {
				width: 90px;
			}
		}

		/*高さ・位置・透過が変化して線が上から下に動く*/
		@keyframes pathmove {
			0% {
				width: 0;
				right: -20px;
				opacity: 0;
			}

			70% {
				width: 60px;
				opacity: 1;
			}

			100% {
				width: 0;
				right: -150px;
				opacity: 0;
			}
		}
	}
}

.fv__humTarget {
	display: none;
	width: 40px;

	@media screen and (max-width: 1000px) {
		display: block;
	}

	p {
		font-size: 10px;
	}
}

.fv__spMenu {
	position: fixed;
	z-index: 3;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	padding: 36px 20px;
	background-color: f.$txt-color;
	width: 100vw;
	height: 100%;
	transform: translateX(100%);
	transition: transform 0.3s;

	.fv__spMenuItems {
		display: flex;
		flex-direction: column;
		gap: unset;

		li {
			a {
				color: f.$white-color;
				font-size: 2.2rem;
				width: 100%;
				display: block;
				padding: 20px 0;
				line-height: 1.5;
			}
		}
	}

	.fv__spMenuClose {
		width: 30px;
		align-self: flex-end;

		img {
			width: 100%;
		}

		p {
			color: f.$white-color;
			font-size: 10px;
			text-align: center;
			padding-bottom: 24px;
		}
	}

	.fv__spInstaBox {
		display: flex;

		p {
			margin-left: 15px;
			font-size: 2.2rem;
		}
	}

	.fv__spMenuIconInsta {
		fill: f.$white-color;
		width: 25px;
	}

	.fv__spBtn {
		width: 350px;
		padding: 15px 45px 15px 25px;
		color: f.$white-color;
		border: 1px solid f.$white-color;

		@media screen and (max-width: 450px) {
			width: 70%;
		}

		&::after {
			background-color: f.$white-color;
		}

		.fv__spMenuMailIcon {
			transform: translate(9px, 3px);
		}
	}

	.fv__spMenuMailIcon {
		fill: f.$white-color;
		width: 25px;
	}

	.fv__spMenuAddress {
		display: flex;
		flex-direction: column;
		margin-top: 40px;
		gap: 10px;
		color: f.$white-color;
		font-size: 1.6rem;

		dl {
			display: flex;
			gap: 15px;

			dt {
				width: 66px;
				display: flex;
				justify-content: space-between;
			}
		}
	}
}

.fv__nav--on {
	.fv__spMenu {
		transform: translateX(0%);
		transition: transform 0.3s;
		overflow-y: scroll;
	}
}
