//about.scss
@use "../../setting/setting" as f;

.about {
	background: url(../img/about-back-pc.jpg) center center / cover no-repeat;
	position: relative;
}

.about__gold {
	position: absolute;
	left: 0;
	top: -150px;
	width: 400px;
	opacity: 0.7;
	z-index: -1;

	@media screen and (max-width: 1000px) {
		max-width: 375px;
		left: unset;
		right: 0;
	}
}

.about__inner {
	padding: 125px 20px 100px;

	@media screen and (max-width: 600px) {
		padding: 84px 0 120px;
	}
}

.about__title {
	text-align: center;
	color: f.$txt-color;

	@media screen and (max-width: 600px) {
		font-size: 2.4rem;
	}
}

.about__contentsBox {
	display: flex;
	column-gap: 100px;
	margin-top: 96px;

	@media screen and (max-width: 1000px) {
		flex-direction: column;
		margin-top: 40px;
	}
}

.about__contentsLeftImg {
	position: relative;
}

.about__contentsTitle {
	margin-top: 48px;
	line-height: 1.4;

	@media screen and (max-width: 1000px) {
		padding: 0 20px;
	}

	@media screen and (max-width: 600px) {
		font-size: 2.2rem;
	}
}

.about__contentsText {
	margin-top: 36px;
	letter-spacing: 0.2em;

	@media screen and (max-width: 1000px) {
		padding: 0 20px;
	}
}

.about__contentsBtn {
	display: inline-block;
	margin-top: 64px;
	padding: 20px 60px;
	border: 1px solid #000000;
	color: #000000;
	position: relative;

	@media screen and (max-width: 1000px) {
		margin-left: 32px;
	}

	@media screen and (max-width: 600px) {
		width: 80%;
		text-align: center;
		padding: 20px 30px;
	}

	&::after {
		position: absolute;
		top: 50%;
		right: -30px;
		content: "";
		width: 60px;
		height: 1px;
		background-color: #000000;
	}

	&:hover::after {
		transform: translate(15px);
		transition: transform 0.3s;
	}
}

.about__contentsRight {
	margin-top: 88px;

	@media screen and (max-width: 1000px) {
		text-align: right;
		margin-top: 72px;
	}

	img {
		@media screen and (max-width: 1000px) {
			width: 90%;
		}
	}
}

picture {
	img {
		width: 100%;
	}
}
